import {useState} from 'react'

import Btn from './components/btn'
import Tasks from './components/tasks'
import AddForm from './components/addForm'


function App() {

  const [showAdd, setShowAdd] = useState(true);
  const [showPending, setShowPending] = useState(true);
  const [showDone, setShowDone] = useState(false);

  const [pending, setPending] = useState( localStorage.getItem('pend') ? JSON.parse( localStorage.getItem('pend') ) :[]);
  const [done, setDone] = useState( localStorage.getItem('done') ? JSON.parse( localStorage.getItem('done') ) : [] );

  const addPending = (text) => {
    let temp = [...pending, text];
    setPending(temp);
    localStorage.setItem('pend', JSON.stringify(temp) );
  }

  const addDone = (text) => {
    let temp = [...done, text];
    setDone(temp);
    localStorage.setItem('done', JSON.stringify(temp));

  }

  const removePending = (idx) => {
    let temp = pending;
    temp.splice(idx, 1);
    setPending([...temp]);
    localStorage.setItem('pend', JSON.stringify(temp) );
  }

  const removeDone = (idx) => {
    let temp = done;
    temp.splice(idx, 1);
    setDone([...temp]);
    localStorage.setItem('done', JSON.stringify(temp));
  }

  const toDone = (idx) => {
    addDone(pending[idx]);
    removePending(idx);

  }

  const toPending = (idx) => {
    addPending(done[idx]);
    removeDone(idx);

  }

  return (
    <div className="App">


      <div className="pending">
          <Btn text={'Tasks'}
          classN={ showPending ? 'tasks-btn-active' : 'tasks-btn' }
          handeClick={() => setShowPending(!showPending)}/>

          {showPending ? <Tasks data={pending} dltfn={removePending} change={toDone}/> : null}
      </div>
      <div className="done">
          <Btn text={'Done'}
          classN={ showDone ? 'done-btn-active' : 'done-btn'}
           handeClick={() => setShowDone(!showDone)}/>

          {showDone ? <Tasks data={done} dltfn={removeDone} change={toPending}/> : null}
      </div>
      <div className="add">
          <Btn text={'Add'}
          classN={showAdd ? 'add-btn-active' :'add-btn'}
          handeClick={() => setShowAdd(!showAdd)}/>

          {showAdd ? <AddForm handleSubmit={addPending}/> : null}
      </div>
    </div>


  );
}

export default App;
